import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Conversation } from '@twilio/conversations';

import GenericMessage from '../generic-message/GenericMessage';
import WriteAMessage from '../svg/WriteAMessage';
import TwilioService from '../../service/TwilioService';

// Styles
import './styles/chat-connecting-window.scss';
import ChatWindow from '../chat-window/ChatWindow';

class ChatConnectingWindow extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        accountIdentifier: PropTypes.string,
        i18n: PropTypes.object,
    };

    state = {
        shouldShowConnectingPage: true,
        shouldShowErrorPage: false,
        shouldShowChatWindowPage: false,
    };

    render() {
        const {
            shouldShowConnectingPage,
            shouldShowErrorPage,
            shouldShowChatWindowPage,
        } = this.state;

        return (
            <>
                {shouldShowConnectingPage && this.renderConnectingPage()}
                {shouldShowChatWindowPage && this.renderChatWindowPage()}
                {shouldShowErrorPage && this.renderErrorPage()}
            </>
        );
    }

    componentDidMount() {
        this.initConversation();
    }

    renderConnectingPage = () => {
        const { contextCls, i18n, ...restProps } = this.props;
        const headText = i18n.getTranslation(
            'chooseOptionWindow.connectingPage.genericMessage.title',
        );

        return (
            <GenericMessage
                contextCls={cx(this.baseCls, contextCls)}
                shouldShowMinimizeBtn
                renderIcon={() => <WriteAMessage />}
                headText={headText}
                i18n={i18n}
                {...restProps}
            />
        );
    };

    renderChatWindowPage = () => {
        const { contextCls, ...restProps } = this.props;

        return (
            <ChatWindow
                contextCls={cx(this.baseCls, contextCls)}
                shouldShowEmailInput={!this.isLoggedIn}
                {...restProps}
            />
        );
    };

    renderErrorPage = () => {
        const { contextCls, ...restProps } = this.props;

        return (
            <GenericMessage
                contextCls={cx(this.baseCls, contextCls)}
                icon="WarningAlert"
                windowTitle="Chat with an agent"
                headText="There was a problem connecting to chat support"
                subText="Please try again later."
                {...restProps}
            />
        );
    };

    handleJoined = () => {
        this.setState({
            shouldShowConnectingPage: false,
            shouldShowErrorPage: false,
            shouldShowChatWindowPage: true,
        });
    };

    handleError = () => {
        this.setState({
            shouldShowConnectingPage: false,
            shouldShowErrorPage: true,
            shouldShowChatWindowPage: false,
        });
    };

    initConversation = async () => {
        try {
            const conversation = await TwilioService.getConversation();

            if (conversation.status === 'joined') {
                this.handleJoined();
            } else {
                conversation.on(
                    Conversation.participantJoined,
                    this.handleJoined,
                );
            }
        } catch {
            this.handleError();
        }
    };

    baseCls = 'chat-connecting-window';
}

export default ChatConnectingWindow;
